//cars reducer

const carsReducerDefaultValue = [];
export default (state = carsReducerDefaultValue, action) => {
    switch (action.type) {
        case 'ADD_CAR' :
            return [
                ...state,
                action.car
            ];
        case 'REMOVE_CAR' :
            return state.filter(({ id }) => id !== action.id);
        case 'EDIT_CAR':
            return state.map((car) => {
                if (car.id === action.id) {
                    return {
                        ...car,
                        ...action.updates
                    };
                } else {
                    return car;
                }
            });
        default :
            return state;
    }
};
