//sort by date
const sortByDate = () => ({
    type: 'SORT_BY_DATE'
});
//sort by amount
const sortByAmount = () => ({
    type: 'SORT_BY_AMOUNT'
});
//set start date
const setStartDate = (startDate) => ({
    type: 'SET_START_DATE',
    startDate
});
//set start date
const setEndDate = (endDate) => ({
    type: 'SET_END_DATE',
    endDate
});
//set text filter
const setTextFilter = (text = '') => ({
    type: 'SET_TEXT_FILTER',
    text
});

export {sortByAmount, sortByDate, setTextFilter, setEndDate, setStartDate};