import React from "react";
import CarListItem from "./CarListItem";
import { connect } from 'react-redux';
import Table from "react-bootstrap/Table";
import GetVisibleCars from "../selectors/GetVisibleCars";
import 'bootstrap/dist/css/bootstrap.min.css';

const CarList = (props) => (
    <div>
        <Table striped bordered hover>
            <thead>
            <tr>
                <th>Brand</th>
                <th>Model</th>
                <th>Price</th>
                <th>Created At</th>
                <th>Remove</th>
            </tr>
            </thead>
            <tbody>
            {props.cars.map((car) => {
                return <CarListItem key={car.id} {...car}/>
            })}
            </tbody>
        </Table>
    </div>
);

const mapStateToProp = (state) => ({
    cars: GetVisibleCars(state.cars, state.filters),
});
const ConnectedCarList = connect(mapStateToProp)(CarList);

export default ConnectedCarList;