//get visible cars
export default (cars, { text, sortBy, startDate, endDate}) => {
    return cars.filter((car) => {
        const startDateMatch = typeof startDate !== 'number' || car.createdAt >= startDate;
        const endDateMatch = typeof endDate !== 'number' || car.createdAt <= startDate;
        const textMatch = car.model.toLowerCase().includes(text.toLowerCase());

        return startDateMatch && endDateMatch && textMatch;
    }).sort((a, b) => {
        if(sortBy === 'date') {
            return a.createdAt < b.createdAt ? 1 : -1;
        } else if(sortBy === 'amount') {
            return a.price < b.price ? 1 : -1;
        }
    });
};