import uuid from "uuid";

//add car
const addCar = ({ model = '', brand = '', price = 0, createdAt = 0, } = {}) => ({
    type: 'ADD_CAR',
    car: {
        id: uuid(),
        model,
        brand,
        price,
        createdAt
    }
});

//remove car
const removeCar = ({ id } = {}) => ({
    type: 'REMOVE_CAR',
    id
});

//edit car
const editCar = (id, updates) => ({
    type: 'EDIT_CAR',
    id,
    updates
});

export { removeCar, editCar, addCar };